import React, {Component} from 'react';
import axios from 'axios';
import ViewBooking from './bookingDisplay';

const burl = "http://developerbasicrestapi.herokuapp.com/orders";


class Booking extends Component{
    constructor(){
        super()

        this.state={
            orders:''
        }
    }
    render(){
        console.log(this.state)
        return(
            <div className="container">
                <ViewBooking orderdata={this.state.orders}/>
            </div>
        )
    }


    async componentDidMount(){
        var hotelid =this.props.match.params.id
        let response= await axios.get(`${burl}`)
         this.setState({orders:response.data})

    }

}


export default Booking;