import React,{Component} from 'react';
import './Search.css';
import {withRouter} from 'react-router-dom';

const lurl ="/city" ;
const rurl ="https://developerbasicrestapi.herokuapp.com/resturant?city="

class Search extends Component{
    constructor(){
        super()

        this.state={
            location:'',
            resturant:'',
        }

        
    }
     //Display City Data
    renderCity =(data) =>{
        if(data){
            return data.map((item) =>{
                return(
                    <option value ={item.city}>
                        {item.name}||{item.city_name}
                    </option>
                )
            })
        }
    }


    //Display Resturant Data
    renderResturant =(data) =>{
        if(data){
            return data.map((item) =>{
                return(
                    <option value ={item._id}>
                        {item.name}||{item.locality}
                    </option>
                )
            })
        }
    }
    
    //load resturant wrt city
    handleCity =(event) =>{
        const cityid = event.target.value
        const rrurl = `${rurl}${cityid}`
        fetch(rrurl,{Method:'GET'})
        .then((res) => res.json())
        .then((data) =>{this.setState({resturant:data})})
    
    }
    
    handleRest=(event) =>{
        
        this.props.history.push(`/details/${event.target.value}`)
    }
    render(){
        console.log(this.state.location);
        return(
            <div className="container2">
            <div id="edurekalogo">
                e!
            </div>
        <div className="title">
            Find the best Resturants, Cafes and Bars
        
        </div>
              <div className="location ">
                 <select className="locationdropdown" onChange={this.handleCity} >
                     <option>Select Location</option>
                    {this.renderCity(this.state.location)}
                 </select>
                 <select className="locationdropdown" onChange={this.handleRest} >
                     <option>Select Resturant</option>
                    {this.renderResturant(this.state.resturant)}
                 </select>
    
           
        </div>
        </div>
        )
    }
    //Call City Api on page load
    componentDidMount(){
        fetch(lurl,{Method:'GET'})
        .then((res) => res.json())
        .then((data) =>{this.setState({location:data})})
    }
}


export default withRouter(Search);